.dialogs {
    display: grid;
    grid-template-columns: 5fr 6fr;
    margin-top: 13%;
    color: #2a1010;
}

.dialog {
    display: flex;
    justify-content: start;
    align-items: center;
    margin-left: 9%;
    font-size: 25px;
    font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, Courier, monospace;
}

.dialog a {
    text-decoration: none;
    color: #7c0167;
    text-align: left;
    margin-left: 37px;
}

.dialog a:hover {
    color: red;
}

.dialog a:active {
    color: darkviolet;
}

.dialog img {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    margin: 7px;
}

.messange {
    font-size: 25px;
    font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, Courier, monospace;
}